import React from "react";
import HeaderFile from "~/components/HeaderFile";
import { getDirectusClient } from "~/lib/directus";
import { Link, useLoaderData, useNavigate } from "@remix-run/react";
import {
  directusLoaderLink,
  ISS_DENMARK_HQ,
  DIETARY_PREFERENCES_ENG,
} from "~/utils/constants.js";
import { toast } from "react-toastify";
import Footer from "~/components/Footer";
import DeleteModel from "~/components/deleteModel";
import { authLogout, isLoggedIn } from "~/utils/db.server";
import { sessionStorage } from "~/services/session.server";

export const action = async ({ request }) => {
  await authLogout(request);
};

export const loader = async ({ request }) => {
  const url = new URL(request.url);
  const page = url.searchParams.get("page") || 1;
  await isLoggedIn(request);
  const session = await sessionStorage.getSession(
    request.headers.get("Cookie")
  );
  var sessionkey = session.get("sessionKey");
  const directus = await getDirectusClient(sessionkey);
  const current = await directus.users.me.read({ fields: ["*.*"] });
  const response = await directus.items("Dietary").readByQuery({
    fields: ["*.*.*"],
    limit: 20,
    page: page,
    meta: ["total_count"],
  });
  return {
    data: response?.data,
    currentPage: page,
    pagination: response?.meta,
    location_id: current?.selected_locations?.id,
  };
};

export function meta() {
  return { title: "Manage Dietary Preferences" };
}

export default function DietaryManage() {
  const limitTable = 10;
  const paginationCount = 20;
  var pagesCount = [];
  const { data, currentPage, pagination, location_id } = useLoaderData();
  const [selected, setSelected] = React.useState([]);
  const [allselect, setAllSelect] = React.useState(false);
  const [modalIsOpen, setIsOpen] = React.useState(false);
  const navigate = useNavigate();
  const numPages =
    pagination?.total_count % paginationCount === 0
      ? pagination?.total_count % paginationCount == 0
        ? Math.floor(pagination?.total_count / paginationCount)
        : pagination?.total_count % paginationCount
      : Math.floor(pagination?.total_count / paginationCount) + 1;

  for (let i = 1; i <= numPages; i++) {
    pagesCount.push(
      <Link
        to={`/dietary-requirements/manage?page=${i}`}
        aria-current="page"
        key={i}
        className={`relative rounded-md w-8 h-8 flex justify-center items-center font-semibold text-sm text-gray-500 focus:z-20 ${
          i === parseInt(currentPage) ? " bg-gray-200" : "hover:bg-gray-100"
        }`}
      >
        {i}
      </Link>
    );
  }

  let dietaryDetailsLeft = [];
  let dietaryDetailsRight = [];

  const selectHandle = (props) => {
    return selected.includes(props)
      ? setSelected(selected.filter((word) => word !== props))
      : setSelected([...selected, props]);
  };

  const markall = () => {
    setAllSelect(!allselect);
    !allselect ? setSelected(data?.map((e) => e?.id)) : setSelected([]);
  };

  const handleNavigation = (e) => {
    setSelected([]);
    navigate(`/dietary-requirements/manage?page=${parseInt(currentPage) + e}`);
    return setAllSelect(false);
  };

  const deleteHandle = async () => {
    const directus = directusLoaderLink;
    if (selected?.length === 1) {
      await directus
        .items("Dietary")
        .deleteOne(selected[0])
        .then((res) => {
          navigate(".", { replace: true });
          setSelected([]);
          toast.success("Deleted successfully");
          window.location.reload();
        })
        .catch((err) => {
          console.log(err);
          navigate(".", { replace: true });
          toast.error("Something went wrong");
        });
    } else {
      await directus
        .items("Dietary")
        .deleteMany(selected)
        .then((res) => {
          toast.success("Deleted successfully");
          navigate(".", { replace: true });
          setSelected([]);
          window.location.reload();
        })
        .catch((err) => {
          console.log(err);
          toast.error("Something went wrong");
          navigate(".", { replace: true });
        });
    }
  };

  data?.map((e, i) => {
    if (i < limitTable) {
      dietaryDetailsLeft.push(
        <tr
          key={e?.id}
          className={
            !selected.includes(e?.id) === true
              ? "bg-gray-50"
              : "bg-transparent hover:bg-gray-50"
          }
        >
          <td className=" relative w-12 px-6 sm:w-16 sm:px-8">
            <input
              type="checkbox"
              title="single-record"
              value={e?.name}
              onChange={(e) => {}}
              checked={!selected.includes(e?.id) === true ? false : true}
              onClick={() => selectHandle(e?.id)}
              className="absolute left-4 top-1/2 -mt-2 h-4 w-4 rounded border-gray-300 text-primary focus:ring-primary sm:left-6"
            />
          </td>
          <td
            className={`cursor-pointer py-4 pr-3 text-sm font-medium ${
              !selected.includes(e?.id) === true
                ? "text-gray-900"
                : "text-primary"
            } `}
            onClick={() => navigate(`/dietary-requirements/edit?id=${e?.id}`)}
          >
            {e.name}
          </td>
        </tr>
      );
    } else
      dietaryDetailsRight.push(
        <tr
          key={e.id}
          className={
            !selected.includes(e.id) === true
              ? "bg-gray-50"
              : "bg-transparent hover:bg-gray-50"
          }
        >
          <td className=" relative w-12 px-6 sm:w-16 sm:px-8">
            <input
              type="checkbox"
              title="single-record"
              value={e?.name}
              onChange={(e) => {}}
              checked={!selected.includes(e?.id) === true ? false : true}
              onClick={() => selectHandle(e?.id)}
              className="absolute left-4 top-1/2 -mt-2 h-4 w-4 rounded border-gray-300 text-primary focus:ring-primary sm:left-6"
            />
          </td>
          <td
            className={`py-4 pr-3 text-sm font-medium ${
              !selected.includes(e?.id) === true
                ? "text-gray-900"
                : "text-primary"
            } `}
            onClick={() => navigate(`/dietary-requirements/edit?id=${e?.id}`)}
          >
            {e.name}
          </td>
        </tr>
      );
  });

  return (
    <div className="page-content">
      <HeaderFile
        logout
        title
        noLeftLogo={ISS_DENMARK_HQ?.includes(location_id) ? true : false}
      />
      <div className="main-content-area">
        <div className="page__wrap py-5 max-w-[1440px] px-4 mx-auto ">
          <div className="flex justify-between items-center gap-5">
            <div className="flex justify-start items-center gap-3">
              <div
                className="cursor-pointer text-xl"
                onClick={() => navigate("/")}
              >
                <span>
                  <i className="ri-home-4-line"></i> /
                </span>
              </div>
              <div>
                <h1 className="text-xl font-semibold text-primary">
                  {DIETARY_PREFERENCES_ENG}
                </h1>
              </div>
            </div>
            <div className="flex justify-between items-center gap-5">
              {selected?.length ? (
                <div>
                  <p>Selected {selected.length}</p>
                </div>
              ) : null}

              <button
                type="button"
                title="Delete Dietary"
                onClick={(e) =>
                  allselect || selected?.length ? setIsOpen(true) : null
                }
                className={`w-10 h-10 min-w-[40px] min-h-[40px] inline-flex items-center justify-center rounded-full border border-transparent
                                ${
                                  allselect || selected?.length
                                    ? "bg-red-600  hover:bg-red-700  focus:ring-red-600"
                                    : "bg-gray-400 cursor-default"
                                } focus:outline-none focus:ring-2 text-sm font-medium text-white shadow-sm
                                 focus:ring-offset-2 sm:w-auto`}
              >
                <span>
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    viewBox="0 0 24 24"
                    fill="none"
                    stroke="currentColor"
                    strokeWidth="2"
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    className="feather feather-trash w-5 h-5"
                  >
                    <polyline points="3 6 5 6 21 6"></polyline>
                    <path d="M19 6v14a2 2 0 0 1-2 2H7a2 2 0 0 1-2-2V6m3 0V4a2 2 0 0 1 2-2h4a2 2 0 0 1 2 2v2"></path>
                  </svg>
                </span>
              </button>
              <Link
                to={"/dietary-requirements/create"}
                title="Add New Dietary"
                className="w-10 h-10 min-w-[40px] min-h-[40px] inline-flex items-center justify-center rounded-full border border-transparent bg-primary text-sm font-medium text-white shadow-sm hover:bg-primary focus:outline-none focus:ring-2 focus:ring-primary focus:ring-offset-2 sm:w-auto"
              >
                <span>
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    fill="none"
                    viewBox="0 0 24 24"
                    strokeWidth="2"
                    stroke="currentColor"
                    className="w-6 h-6"
                  >
                    <path
                      strokeLinecap="round"
                      strokeLinejoin="round"
                      d="M19 7.5v3m0 0v3m0-3h3m-3 0h-3m-2.25-4.125a3.375 3.375 0 11-6.75 0 3.375 3.375 0 016.75 0zM4 19.235v-.11a6.375 6.375 0 0112.75 0v.109A12.318 12.318 0 0110.374 21c-2.331 0-4.512-.645-6.374-1.766z"
                    />
                  </svg>
                </span>
              </Link>
            </div>
          </div>
          {!!dietaryDetailsLeft && dietaryDetailsLeft?.length <= 0 ? (
            <div className="flex justify-center items-center self-center">
              <span className="text-center bg-slate-200 p-2 rounded-md ">
                No data found
              </span>
            </div>
          ) : (
            <div className="mt-6 flex flex-col">
              <div className="grid grid-cols-12 gap-5 overflow-x-auto xl:gap-24">
                {/* left table */}
                <div className="col-span-12 md:col-span-6">
                  <div className="inline-block min-w-full align-middle">
                    <div className="relative border rounded-md border-gray-200 overflow-hidden shadow-sm ring-1 ring-black ring-opacity-5 md:rounded-lg">
                      <table className="min-w-full divide-y  divide-gray-300">
                        <thead className="bg-gray-50">
                          <tr>
                            <th
                              scope="col"
                              className="relative w-12 px-6 sm:w-16 sm:px-8"
                            >
                              <input
                                type="checkbox"
                                title="select-all-record"
                                onClick={() => markall()}
                                onChange={(e) => {}}
                                checked={allselect ? true : false}
                                className="absolute left-4 top-1/2 -mt-2 h-4 w-4 rounded border-gray-300 text-primary focus:ring-primary sm:left-6"
                              />
                            </th>
                            <th
                              scope="col"
                              className="py-3.5 pr-3 text-left text-sm font-semibold text-gray-900 xl:min-w-[12rem]"
                            >
                              Name
                            </th>
                          </tr>
                        </thead>
                        <tbody className="divide-y divide-gray-200 bg-white">
                          {dietaryDetailsLeft}
                        </tbody>
                      </table>
                    </div>
                  </div>
                </div>

                {/* Right table */}
                {data?.length > limitTable ? (
                  <div className="col-span-12 md:col-span-6">
                    <div className="inline-block min-w-full align-middle">
                      <div className="relative border rounded-md border-gray-200 overflow-hidden shadow-sm ring-1 ring-black ring-opacity-5 md:rounded-lg">
                        <table className="min-w-full divide-y  divide-gray-300">
                          <thead className="bg-gray-50">
                            <tr>
                              <th
                                scope="col"
                                className="relative w-12 px-6 sm:w-16 sm:px-8"
                              >
                                <input
                                  type="checkbox"
                                  title="select-all-record"
                                  onClick={() => markall()}
                                  onChange={(e) => {}}
                                  checked={allselect ? true : false}
                                  className="absolute left-4 top-1/2 -mt-2 h-4 w-4 rounded border-gray-300 text-primary focus:ring-primary sm:left-6"
                                />
                              </th>
                              <th
                                scope="col"
                                className="py-3.5 pr-3 text-left text-sm font-semibold text-gray-900 xl:min-w-[12rem]"
                              >
                                Name
                              </th>
                            </tr>
                          </thead>
                          <tbody className="divide-y divide-gray-200 bg-white">
                            {dietaryDetailsRight}
                          </tbody>
                        </table>
                      </div>
                    </div>
                  </div>
                ) : null}
              </div>
              {/* pagination */}
              {numPages > 1 ? (
                <div className="mt-8 mx-auto ">
                  <nav
                    className="isolate inline-flex space-x-1 rounded-md"
                    aria-label="Pagination"
                  >
                    <button
                      onClick={
                        data && !data.length && currentPage <= 1
                          ? null
                          : () => handleNavigation(-1)
                      }
                      disabled={currentPage <= 1}
                      data-nav-operation="previous"
                      className="relative rounded-md  bg-gray-50 w-8 h-8 flex justify-center items-center font-semibold text-sm text-gray-400 hover:bg-gray-100 focus:z-20"
                    >
                      <span className="sr-only">Previous</span>
                      <svg
                        className="h-5 w-5"
                        strokeWidth="2"
                        xmlns="http://www.w3.org/2000/svg"
                        viewBox="0 0 20 20"
                        fill="currentColor"
                        aria-hidden="true"
                      >
                        <path
                          fillRule="evenodd"
                          d="M12.79 5.23a.75.75 0 01-.02 1.06L8.832 10l3.938 3.71a.75.75 0 11-1.04 1.08l-4.5-4.25a.75.75 0 010-1.08l4.5-4.25a.75.75 0 011.06.02z"
                          clipRule="evenodd"
                        />
                      </svg>
                    </button>
                    {pagesCount}
                    <button
                      onClick={
                        data && currentPage > numPages - 1
                          ? null
                          : () => handleNavigation(1)
                      }
                      disabled={
                        data && !data.length && currentPage > numPages - 1
                      }
                      data-nav-operation="next"
                      className="relative rounded-r-md bg-gray-50 rounded-md w-8 h-8 flex justify-center items-center font-semibold text-sm text-gray-500 hover:bg-gray-100 focus:z-20"
                    >
                      <span className="sr-only">Next</span>
                      <svg
                        className="h-5 w-5"
                        xmlns="http://www.w3.org/2000/svg"
                        viewBox="0 0 20 20"
                        strokeWidth="2"
                        fill="currentColor"
                        aria-hidden="true"
                      >
                        <path
                          fillRule="evenodd"
                          d="M7.21 14.77a.75.75 0 01.02-1.06L11.168 10 7.23 6.29a.75.75 0 111.04-1.08l4.5 4.25a.75.75 0 010 1.08l-4.5 4.25a.75.75 0 01-1.06-.02z"
                          clipRule="evenodd"
                        />
                      </svg>
                    </button>
                  </nav>
                </div>
              ) : null}
            </div>
          )}
        </div>
      </div>
      {modalIsOpen ? (
        <DeleteModel open={setIsOpen} deleteHandle={deleteHandle} />
      ) : null}
      <Footer />
    </div>
  );
}
